import React, { useState } from "react";
import "./two.less";

const LIST = [
  { id: "拖拽元素1", text: "第一个元素", allowMove: true },
  { id: "拖拽元素2", text: "第二个元素", allowMove: false },
  { id: "拖拽元素3", text: "第三个元素", allowMove: true }
];

const Two: React.FC = () => {
  const [draggableData, setDraggableData] = useState<any>({}); // 拖拽项的数据

  const handleDragStart = (e: any, data: any) => {
    // e.persist();
    e.target.classList.add("draggable-style");
    // 数据交换
    e.dataTransfer.setData("data", JSON.stringify(data));
  };
  
  const handleDragEnd = (e: any) => {
    // e.persist();
    e.target.classList.remove("draggable-style");
  };

  const handleDragEnter = (e: any) => {
    e.target.classList.add("draggable-style");
  };

  const handleDragLeave = (e: any) => {
    e.target.classList.remove("draggable-style");
  };

  const handleDrop = async (e: any) => {
    e.target.classList.remove("draggable-style");
    // 获取携带数据
    const data = JSON.parse(e.dataTransfer.getData("data"));
    console.log("value", data, e);
    setDraggableData(data);
  };

  const handleDragover = (e: any) => {
    e.preventDefault();
  };

  return <div className="page-content">
    <div>
      {
        LIST.map((item: any, index: number) => {
          return <div
            key={index}
            draggable={item.allowMove ? true : false}
            onDragStart={e => handleDragStart(e, item)}
            onDragEnd={handleDragEnd}
          >
            {item.text}
          </div>;
        })
      }
    </div>
    <div
      onDragEnter={handleDragEnter}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      onDragOver={handleDragover}
    >
      {draggableData.text}
    </div>
  </div>;
};

export default Two;
